"use client"

import Link from "next/link"
import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Sheet, SheetContent, SheetTrigger } from "@/components/ui/sheet"
import { Menu } from "lucide-react"
import { ThemeToggle } from "@/components/theme-toggle"

const navItems = [
  { label: "Movies", path: "/movies" },
  { label: "Upcoming Movies", path: "/upcoming" },
  { label: "Most Popular", path: "/most-popular" },
  { label: "Contact", path: "/contact" },
  { label: "Store", path: "/store" },
  { label: "Settings", path: "/settings" },
]

export default function Navbar() {
  const [isOpen, setIsOpen] = useState(false)
  const [isScrolled, setIsScrolled] = useState(false)

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 0) {
        setIsScrolled(true)
      } else {
        setIsScrolled(false)
      }
    }

    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  return (
    <header className={`fixed top-0 z-50 w-full transition-colors duration-300 ${
      isScrolled ? 'bg-white dark:bg-black' : 'bg-gradient-to-b from-white/80 dark:from-black/80 to-transparent'
    }`}>
      <div className="flex h-16 items-center px-4 md:px-8">
        <Link href="/" className="mr-10">
          <span className="text-2xl font-bold text-orange-600 font-poppins tracking-tight">RecFlix</span>
        </Link>
        
        {/* Desktop Navigation */}
        <nav className="hidden md:flex md:items-center md:space-x-6">
          {navItems.map((item) => (
            <Link
              key={item.path}
              href={item.path}
              className="text-sm font-medium text-gray-600 dark:text-gray-200 transition-colors hover:text-black dark:hover:text-white"
            >
              {item.label}
            </Link>
          ))}
        </nav>

        {/* Mobile Navigation */}
        <Sheet open={isOpen} onOpenChange={setIsOpen}>
          <div className="flex items-center gap-4 ml-auto">
            <div className="hidden md:flex items-center gap-2">
              <Link href="/login">
                <Button variant="ghost" className="text-black dark:text-white">
                  Log in
                </Button>
              </Link>
              <Link href="/signup">
                <Button className="bg-orange-600 hover:bg-orange-700 text-white">
                  Sign up
                </Button>
              </Link>
            </div>
            <ThemeToggle />
            <SheetTrigger asChild className="md:hidden">
              <Button variant="ghost" size="icon" className="text-black dark:text-white">
                <Menu className="h-6 w-6" />
                <span className="sr-only">Toggle menu</span>
              </Button>
            </SheetTrigger>
          </div>
          <SheetContent side="right" className="bg-white/95 dark:bg-black/95 border-gray-200 dark:border-gray-800">
            <nav className="flex flex-col space-y-4">
              {navItems.map((item) => (
                <Link
                  key={item.path}
                  href={item.path}
                  className="text-sm font-medium text-gray-600 dark:text-gray-200 transition-colors hover:text-black dark:hover:text-white"
                  onClick={() => setIsOpen(false)}
                >
                  {item.label}
                </Link>
              ))}
              <div className="pt-4 mt-4 border-t border-gray-200 dark:border-gray-800">
                <Link
                  href="/login"
                  className="block text-sm font-medium text-gray-600 dark:text-gray-200 transition-colors hover:text-black dark:hover:text-white mb-2"
                  onClick={() => setIsOpen(false)}
                >
                  Log in
                </Link>
                <Link
                  href="/signup"
                  className="block text-sm font-medium text-orange-600 transition-colors hover:text-orange-700"
                  onClick={() => setIsOpen(false)}
                >
                  Sign up
                </Link>
              </div>
            </nav>
          </SheetContent>
        </Sheet>
      </div>
    </header>
  )
}
